<template>
  <div class="container">
    <div class="wrap">
        <warning :warningShow="warningShow" :warningMsg='warningMsg'/>
        <div class="title">Connect, share and <br/>explore with us.</div>
        <div class="fromBox">
            <div class="fromItem">
                <div class="fromItemInput">
                    <img src="@/assets/img/sign/email.png" alt="" class="emailIcon">
                    <input type="text" name="email" id="email" value="" placeholder="HSBC email (required)" v-model="email" @change="checkFrom">
                </div>
            </div>
            <div class="fromItem">
                <div class="fromItemInput">
                    <img src="@/assets/img/sign/username.png" alt="" class="usernameIcon">
                    <input type="text" name="username" id="username" value="" placeholder="New login username (required)" v-model="username" @change="checkFrom">
                </div>
            </div>
            <div class="fromItem">
                <div class="fromItemInput">
                    <img src="@/assets/img/sign/family_code.png" alt="" class="familyIcon">
                    <select name="market_code" id="market_code" v-model="market_code" @change="checkFrom">
                        <option value="">Family member/HSBC area code</option>
                        <option value="2">HSBC employee account</option>
                        <option value="1">Supplementary family account</option>
                    </select>
                    <i></i>
                </div>
                <div class="fromTips">
                    Area code is for HSBC Employees Only.  Please choose Family member if you are registering for a family member.
                </div>
            </div>
            <div class="fromItem">
                <div class="fromItemInput">
                    <img src="@/assets/img/sign/referral_code.png" alt="" class="codeIcon">
                    <input type="text" name="referral_code" id="referral_code" value="" placeholder="Referral code (optional)" v-model="referral_code">
                </div>
            </div>
            <div class="fromItem">
                <div class="fromItemInput">
                    <img src="@/assets/img/sign/password.png" alt="" class="passwordIcon">
                    <input type="password" name="password" id="password" value="" placeholder="Create password" v-model="password" @change="checkFrom">
                </div>
            </div>
            <div class="fromItem">
                <div class="fromItemInput">
                    <img src="@/assets/img/sign/password.png" alt="" class="passwordIcon">
                    <input type="password" name="password_true" id="password_true" value="" placeholder="Re-confirm password"  v-model="password_confirm"  @change="checkFrom">
                </div>
                <div class="fromTips">Your password must include: between 8-12 characters, with at least one upper case letter, one lower case letter, one number or special character or both.</div>
            </div>
        </div>
        <div class="privacyBox">
            <a class="privacy" href="" target="_blank">Privacy policy</a>
        </div>
    </div>
    <div class="btns">
        <button class="btn btn01" @click="prev">Back</button>
        <button class="btn btn02" @click="next" v-if="nextBtn">Continue</button>
        <button class="btn btn02 none" v-else>Continue</button>
    </div>
  </div>
</template>

<script>
import warning from '@/components/warning.vue'
export default {
    components:{
      warning
    },
    head(){
        return {
            title:''
        }
    },
    data(){
        return {
          nextBtn:false,
          email:'',
          username:'',
          password:'',
          password_confirm:'',
          market_code:'',
          referral_code:'',

          warningShow:false,
          warningMsg:'',
        }
    },
    mounted(){
        var that = this;
    },
    methods:{
        next:function(){
            var that = this;
            if(!that.email){
                that.warningShow = true;
                that.warningMsg = 'This HSBC email has been registered already, please use another email or select "Forgotten username/password?".';
                setTimeout(function(){
                    that.warningShow = false;
                },2000)
                return false;
            }
            if(!that.username){
                that.warningShow = true;
                that.warningMsg = 'This HSBC email has been registered already, please use another email or select "Forgotten username/password?".';
                setTimeout(function(){
                    that.warningShow = false;
                },2000)
                return false;
            }
            if(that.password != that.password_confirm){
                that.warningShow = true;
                that.warningMsg = 'This HSBC email has been registered already, please use another email or select "Forgotten username/password?".';
                setTimeout(function(){
                    that.warningShow = false;
                },2000)
                return false;
            }
            var query = {email:that.email,username:that.username,password:that.password,market_code:that.market_code,referral_code:that.referral_code};
            
            that.$utils.setCookie('regData',JSON.stringify(query))
            that.$router.push({path:'/sign_up/question'})
        },
        prev:function(){
            var that = this;
            that.$router.go(-1);
        },
        checkFrom:function(){
            var that = this;
            if(that.email && that.username && that.password && that.password_confirm && that.market_code){
                console.log('1')
                that.nextBtn = true;
            }else{
                console.log('2')
                that.nextBtn = false;
            }
        }
    },
}
</script>

<style lang='scss' scoped>
.wrap{
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 1.88rem;
    right: 0;
    padding: 0.8rem 0.37rem 0 0.4rem;
    box-sizing: border-box;
    overflow-y: auto;
    .title{
        font-size: 0.46rem;
        line-height: 0.6rem;
    }
    .fromBox{
            width: 100%;
            margin-top: 0.42rem;
            .fromItem{
                width: 100%;
                margin-bottom: 0.63rem;
                &:last-child{
                    margin-bottom: 0.4rem;
                }
                .fromTitle{
                    width: 100%;
                    height: 0.41rem;
                    line-height: 0.41rem;
                    font-size: 0.32rem;
                    color: #000000;
                }
                .fromItemInput{
                    width: 100%;
                    height: 0.4rem;
                    padding: 0.24rem 0;
                    border-bottom: 1px solid #000000;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    box-sizing: initial;
                    position: relative;
                    .usernameIcon{
                        display: block;
                        width: 0.4rem;
                        height: 0.4rem;
                    }
                    .emailIcon{
                        display: block;
                        width: 0.4rem;
                        height: 0.4rem;
                    }
                    .codeIcon{
                        display: block;
                        width: 0.36rem;
                        height: 0.34rem;
                    }
                    .passwordIcon{
                        display: block;
                        width: 0.4rem;
                        height: 0.4rem;
                    }
                    .familyIcon{
                        display: block;
                        width: 0.4rem;
                        height: 0.4rem;
                    }
                    >input{
                        width: 6rem;
                        height: 0.4rem;
                        line-height: 0.4rem;
                        font-size: 0.32rem;
                        color: #767676;
                        &::placeholder{
                            color: #767676;
                        }
                    }
                    >i{
                        display: block;
                        width: 0.3rem;
                        height: 0.15rem;
                        background: url(../../assets/img/sign/list_arrow.png) no-repeat;
                        background-size: 100% 100%;
                        position: absolute;
                        top: 50%;
                        margin-top: -0.07rem;
                        right: 0;
                    }
                    >select{
                        width: 6rem;
                        height: 0.4rem;
                        line-height: 0.4rem;
                        font-size: 0.32rem;
                        color: #767676;
                        background: transparent;
                    }
                }
                .fromTips{
                    width: 100%;
                    margin-top: 0.23rem;
                    font-size: 0.19rem;
                    color: #767676;
                    line-height: 0.27rem;
                }
            }
        }
    .privacyBox{
        width: 100%;
        height: 0.32rem;
        display: flex;
        align-items: center;
        margin-bottom: 0.2rem;
        .privacy{
            display:inline-block;
            font-size: 0.24rem;
            color: #282014;
            text-decoration: underline;
        }
    }
    
}
.btns{
    width: 100%;
    height: 1.88rem;
    padding: 0.4rem;
    box-sizing: border-box;
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0.02rem;
    .btn{
        width: 3.27rem;
        height: 1.18rem;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 0.32rem;
        &.btn01{
            border:1px solid #000000;
            color: #000000;
            box-sizing: border-box;
            background: #ffffff;
        }
        &.btn02{
            background: #DB0011;
            color: #ffffff;
        }
        &.none{
            background: #D7D8D6;
            color: #ffffff;
        }
    }
    .errorTips{
        width: 100%;
        
    }
}
</style>
