<template>
  <div class="container">
    <top :backShow='true'/>
    <div class="homeWrap">
		<div class="banner">
            <img src="@/assets/img/test/news.png" alt="" width="100%">
        </div>
        <div class="like">
            <img src="@/assets/img/home/like.png" alt="like" class="likeIcon">
            <img src="@/assets/img/utils/star_none.png" alt="like" class="likeIcon">
            <span>1000</span>
        </div>
        <div class="detail" >
            <div class="newsTitle" ref="title">{{detail.title}}</div>
            <div class="newsContent" :style="{top:height}">
                <div class="progress" v-show="progressStatus">
                    <div class="progressValue" :style="{height:progress+'%',top:progressTop + '%'}"></div>
                </div>
                <vuescroll @handle-scroll="handleScroll" ref="vs">
                    <div class="newsContentView" v-html="detail.content"></div>
                </vuescroll>
            </div>
        </div>
    </div>
    <tabbar/>
  </div>
</template>

<script>
import tabbar from '@/components/tabbar.vue'
import top from '@/components/top.vue'
import vuescroll from 'vuescroll';
export default {
    components:{
      tabbar,
      top,
      vuescroll
    },
    head(){
        return {
            title:'',
        }
    },
    data(){
        return {
          height:'0.84rem',
          progress:20,
          id:this.$route.query.id,
          detail:{},
          progressStatus:false,
          progress:0,
          progressTop:0,
        }
    },
    mounted(){
        var that = this;
        that.getNews();
    },
    methods:{
        getNews:function(){
            var that = this;
            that.$http.post(that.$api.articleDetail.url, {id:that.id}).then(res => {
                if(res.code == 1){
                    that.detail = res.data.detail;
                    
                    setTimeout(function(){
                        that.getHeight();
                        
                    },100)
                    
                }else{
                    that.$toast(res.msg);
                }
            })
        },
        getHeight:function(){
            var that = this;
            let height= this.$refs.title.offsetHeight;
            that.height = height +'px';
            that.getScroll();
        },
        getScroll:function(){
            var that = this;
            that.progress = that.$refs["vs"].getCurrentviewDom()[0].offsetHeight / that.$refs["vs"].getCurrentviewDom()[0].scrollHeight * 100;
            if(that.progress == 100){
                that.progressStatus = false;
            }else{
                that.progressStatus = true;
            }
        },
        handleScroll:function(v,h,n){
            var that = this;
            that.progress = v.barSize * 100;
            that.progressTop = v.process * 100 - v.barSize * 100 *  v.process;
        },
    },
}
</script>

<style lang='scss' scoped>
.homeWrap{
    overflow: hidden;
    .banner{
        width: 100%;
        height: 4.82rem;
        overflow: hidden;
    }
    .like{
        width: 100%;
        height: 0.37rem;
        padding: 0 0.4rem;
        margin-top: 0.33rem;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        box-sizing: border-box;
        .likeIcon{
            display: block;
            width: 0.44rem;
            height: 0.37rem;
        }
        >span{
            font-size: 0.19rem;
            color: #9B9B9B;
            margin-left: 0.07rem;
        }
    }
    .detail{
        width: 100%;
        position: absolute;
        top: 5.76rem;
        left: 0;
        bottom: 0;
        right: 0;
        padding: 0 0.23rem 0.4rem 0.21rem;
        box-sizing: border-box;
        .newsTitle{
            width: 100%;
            font-size: 0.46rem;
            line-height: 0.6rem;
            padding-bottom: 0.24rem;
        }
        .newsContent{
            position: absolute;
            left: 0.21rem;
            right: 0.23rem;
            bottom: 0.4rem;
            .progress{
                width: 0.07rem;
                height: 100%;
                background:rgb(216,216,216);
                position: absolute;
                top: 0;
                left: 0;
                z-index: 5;
                .progressValue{
                    width: 100%;
                    background: rgb(219,0,17);
                    position: absolute;
                    top: 0;
                    left: 0;
                }
            }
            .newsContentView{
                position: absolute;
                top: 0;
                left: 0;
                bottom: 0;
                right: 0;
                z-index: 1;
                font-size: 0.28rem;
                color: #767676;
                line-height: 0.38rem;
                padding-left: 0.29rem;
                box-sizing: border-box;
                text-align: left;
                &::-webkit-scrollbar{
                    width: 0.07rem;
                }
                &::-webkit-scrollbar-track {
                    background-color: rgb(216,216,216);
                }
                &::-webkit-scrollbar-thumb {
                    background-color:  rgb(219,0,17); 
                }
            }
        }
    }
}
</style>
