<template>
  <div class="tabbar">
      <div class="tabbarItem" @click="menuClick">
          <img src="@/assets/img/tabbar/Menu.png" alt="Menu" class="menuIcon">
          <span>Menu</span>
      </div>
      <router-link class="tabbarItem" to="/play">
          <img src="@/assets/img/tabbar/Play_and_share.png" alt="Play and share" class="playIcon">
          <span>Play and share</span>
      </router-link>
      <router-link class="tabbarItem" to="/webcasts">
          <img src="@/assets/img/tabbar/Webcasts.png" alt="Webcasts" class="webIcon">
          <span>Webcasts</span>
      </router-link>
      <router-link class="tabbarItem" to="/my">
          <img src="@/assets/img/tabbar/My_profile.png" alt="My profile" class="myIcon">
          <span>My profile</span>
      </router-link>
      <div class="menu" v-if="menuShow">
          <div class="menuClose" @click="menuClose"><img src="@/assets/img/utils/close.png" alt="close"></div>
          <div class="menuTitle">Menu</div>
          <div class="menuList">
              <router-link class="menuItem" to="/my"><span>My profile</span></router-link>
              <router-link class="menuItem" to="/home/articles"><span>Latest articles</span></router-link>
              <router-link class="menuItem" to="/play"><span>Play and share</span></router-link>
              <router-link class="menuItem" to="/my/points"><span>Points and prizes</span></router-link>
              <router-link class="menuItem" to="/webcasts"><span>Well-being webcasts</span></router-link>
              <router-link class="menuItem" to="/my/question"><span>Frequently asked questions</span></router-link>
              <router-link class="menuItem" to="/my/terms"><span>Terms and conditions</span></router-link>
              <router-link class="menuItem" to="/my/contact"><span>Contact us</span></router-link>
              <div class="menuItem" @click="logOut"><span>Log out</span></div>
          </div>
      </div>
  </div>
</template>
<script>
export default {
    props: {

    },
    data(){
        return {
            menuShow:false,
        }
    },
    components: {
        
    },
    async mounted(){
        var that = this;
        
    },
    methods:{
        menuClick:function(){
            var that = this;
            that.menuShow = true;
        },
        menuClose:function(){
            var that = this;
            that.menuShow = false;
        },
        logOut:function(){
            var that = this;
            that.$http.post(that.$api.logout.url, {}).then(res => {
                if(res.code == 1){
                    that.$utils.delCookie('userinfo');
                    that.$utils.delCookie('token');
                    that.$router.push({path:'/logon/welcome'})
                }else{
                    that.$toast(res.msg);
                }
            })
        }
    }
}
</script>
<style lang='scss'>
.tabbar{
    width: 100%;
    height: 0.98rem;
    position: fixed;
    bottom: 0;
    left: 0;
    border-top: 1px solid #eeeeee;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 0.2rem;
    z-index: 5;
    background: #ffffff;
    .tabbarItem{
        .menuIcon{
            display: block;
            width: 0.4rem;
            height: 0.41rem;
            margin: 0 auto;
        }
        .playIcon{
            display: block;
            width: 0.42rem;
            height: 0.41rem;
            margin: 0 auto;
        }
        .webIcon{
            display: block;
            width: 0.41rem;
            height: 0.41rem;
            margin: 0 auto;
        }
        .myIcon{
            display: block;
            width: 0.44rem;
            height: 0.41rem;
            margin: 0 auto;
        }
        >span{
            display: block;
            font-size: 0.19rem;
            color: #1E1F24;
            text-align: center;
            margin-top: 0.1rem;
        }
    }
    .menu{
        width: 100%;
        background: #ffffff;
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 6;
        padding-left: 0.5rem;
        box-sizing: border-box;
        .menuClose{
            width: 0.24rem;
            height: 0.24rem;
            position: absolute;
            top: 0.33rem;
            right: 0.33rem;
            >img{
                display: block;
                width: 100%;
                height: 100%;
            }
        }
        .menuTitle{
            width: 100%;
            font-size: 0.35rem;
            font-weight: 500;
            line-height: 0.8rem;
            margin-left: -0.26rem;
        }
        .menuList{
            width: 100%;
            margin-top: 0.1rem;
            .menuItem{
                width: 100%;
                height: 0.8rem;
                display: flex;
                align-items: center;
                border-bottom:1px solid #979797;
                box-sizing: border-box;
                >span{
                    margin-left: -0.26rem;
                    font-size: 0.3rem;
                }
                &:last-child{
                    border-bottom: 0 none;
                }
            }
        }
    }
}
</style>