<template>
  <div class="container">
    <div class="wrap">
        <div class="title">Terms and conditions</div>
        <div class="timeBox"><span>Last Update Aug.12th 2020</span></div>
        <div class="termsBox">
            <div class="progress">
                <div class="progressValue" :style="{height:progress+'%',top:progressTop + '%'}"></div>
            </div>
            <vuescroll @handle-scroll="handleScroll" ref="vs">
                <div class="termsContent" >
                    <div v-for="(item,index) in 10" :key="index">
                        <div class="termsTitle">1.Terms</div>
                        <div class="termsTxt">
                            This website, https://bootstrapious.com, is operatewd by Ondrej Svestka and Bootstrapious. Throughout the site, the terms we, us and our refer to Ondrej Svestka and Bootstrapious. We offer this website, including all information, tools and services available from this site to you, the user, conditioned upon your acceptance of all terms, conditions, policies and notices stated here.
                        </div>
                    </div>
                </div>
            </vuescroll>
        </div>
    </div>
    <div class="btns">
        <button class="btn btn01" @click="back">Back</button>
        <button class="btn btn02" :class="{success:btnStatus}" @click="next">Continue</button>
    </div>
    <div class="mask" v-if="popStatus">
        <div class="pop">
            <img src="@/assets/img/sign/close.png" alt="close" class="popClose" @click="closePop">
            <div class="popTitle">Terms and conditions</div>
            <div class="popContent">
                <i v-if="!popCheck" @click="popCheckBind"></i>
                <img src="@/assets/img/sign/tick box.png" alt="tick box" class="tick box" v-else @click="popCheckBind">
                <span @click="popCheckBind">I have read and hereby agree to the terms and conditions.</span>
            </div>
            <div class="popBtn" :class="{active:popCheck}" @click="reg">Register</div>
        </div>
    </div>
  </div>
</template>

<script>
import vuescroll from 'vuescroll';
export default {
    components:{
      vuescroll
    },
    head(){
        return {
            title:''
        }
    },
    data(){
        return {
          btnStatus:false,
          progress:20,
          popStatus:false,
          popCheck:false,
          progress:0,
          progressTop:0,
        }
    },
    mounted(){
        var that = this;
        that.getScroll();
    },
    methods:{
        reg:function(){
            var that = this;
            if(!that.popCheck){
                return false;
            }
            var questionData = JSON.parse(that.$utils.getCookie('questionData'));
            var query = JSON.parse(that.$utils.getCookie('regData'));
            query.alists = questionData;
            that.$http.post(that.$api.register.url, query).then(res => {
                if(res.code == 1){
                    that.$utils.setCookie('userinfo',JSON.stringify(res.data.userinfo),Number(res.data.userinfo.expires_in)/3600);
                    that.$utils.setCookie('token',res.data.userinfo.token,Number(res.data.userinfo.expires_in)/3600);
                    that.$utils.delCookie('regData');
                    that.$utils.delCookie('questionData');
                    that.$router.push({path:'/sign/success',params:{type:1}})
                }else{
                    that.$toast(res.msg);
                }
            })
        },
        getScroll:function(){
            var that = this;
            that.progress = that.$refs["vs"].getCurrentviewDom()[0].offsetHeight / that.$refs["vs"].getCurrentviewDom()[0].scrollHeight * 100;
        },
        handleScroll:function(v,h,n){
            var that = this;
            that.progress = v.barSize * 100;
            that.progressTop = v.process * 100 - v.barSize * 100 *  v.process;
            that.btnStatus = true;
        },
        popCheckBind:function(){
            var that = this;
            if(that.popCheck){
                that.popCheck = false;
            }else{
                that.popCheck = true;
            }
        },
        closePop:function(){
            var that = this;
            that.popStatus = false;
        },
        next:function(){
            var that = this;
            if(that.btnStatus){
                that.popStatus = true;
            }
        },
        back:function(){
            var that = this;
            that.$router.go(-1);
        },
    },
}
</script>

<style lang='scss' scoped>
.wrap{
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 1.88rem;
    right: 0;
    padding: 0.8rem 0.4rem 0 0.37rem;
    box-sizing: border-box;
    .title{
        width: 100%;
        line-height: 0.6rem;
        font-size: 0.46rem;
        text-align: center;
    }
    .timeBox{
        width: 100%;
        height: 0.54rem;
        margin-top: 0.4rem;
        display: flex;
        align-items: center;
        justify-content: center;
        >span{
            display: inline-block;
            height: 0.54rem;
            line-height: 0.54rem;
            padding: 0 0.68rem;
            background: #D7D8D6;
            font-size: 0.23rem;
            color: #000000;
        }
    }
    .termsBox{
        position: absolute;
        top: 2.77rem;
        left: 0.37rem;
        bottom: 0;
        right: 0.4rem;
        
        .progress{
            width: 0.07rem;
            height: 100%;
            background:rgb(216,216,216);
            position: absolute;
            top: 0;
            left: 0;
            z-index: 5;
            .progressValue{
                width: 100%;
                background: rgb(219,0,17);
                position: absolute;
                top: 0;
                left: 0;
            }
        }
        .termsContent{
            width: 100%;
            padding-left: 0.4rem;
            box-sizing: border-box;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 1;
            text-align: left;
            &::-webkit-scrollbar{
                    width: 0.03rem;
            }
            &::-webkit-scrollbar-track {
                background-color: rgb(216,216,216);
            }
            &::-webkit-scrollbar-thumb {
                background-color:  rgb(219,0,17); 
            }
            .termsTitle{
                font-size: 0.32rem;
                color: #000000;
                line-height: 0.48rem;
            }
            .termsTxt{
                font-size: 0.28rem;
                line-height: 0.48rem;
                color: #767676;
                margin: 0.15rem 0;
            }
        }
    }
}
.btns{
    width: 100%;
    height: 1.88rem;
    padding: 0.4rem;
    box-sizing: border-box;
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0.02rem;
    .btn{
        width: 3.27rem;
        height: 1.18rem;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 0.32rem;
        &.btn01{
            border:1px solid #000000;
            color: #000000;
            box-sizing: border-box;
            background: #ffffff;
        }
        &.btn02{
            background: #D7D8D6;
            color: #ffffff;
        }
        &.success{
            background: #DB0011;
            color: #ffffff;
        }
    }
    
}
.mask{
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0,0,0,0.4);
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center;
    .pop{
        width: 5.69rem;
        padding: 0.4rem;
        background: #ffffff;
        box-sizing: border-box;
        position: relative;
        .popClose{
            display: block;
            width: 0.2rem;
            height: 0.2rem;
            position: absolute;
            top: 0.24rem;
            right: 0.24rem;
        }
        .popTitle{
            width: 100%;
            line-height: 0.6rem;
            font-size: 0.46rem;
            font-weight: 500;
            text-align: center;
        }
        .popContent{
            width: 100%;
            margin-top: 0.4rem;
            display: flex;
            justify-content: space-between;
            >i{
                display: block;
                width: 0.24rem;
                height: 0.24rem;
                border: 1px solid #000000;
                box-sizing: border-box;
                margin-top: 0.1rem;
            }
            .box{
                display: block;
                width: 0.24rem;
                height: 0.24rem;
                margin-top: 0.1rem;
            }
            >span{
                width: 4.53rem;
                font-size: 0.28rem;
                line-height: 0.36rem;
            }
        }
        .popBtn{
            width: 100%;
            height: 0.88rem;
            margin-top: 0.4rem;
            background: #D7D8D6;
            font-size: 0.32rem;
            color: #ffffff;
            display: flex;
            align-items: center;
            justify-content: center;
            &.active{
                background: #DB0011;
            }
        }
    }
}
</style>
