<template>
  <div class="container">
    <top :backShow='true'/>
    <div class="homeWrap">
        <div class="title">Latest articles</div>
        <div class="bannerBox">
            <div class="banner">
                <div class="swiper-container">
                    <div class="swiper-wrapper">
                        <div class="swiper-slide" v-for="(item,index) in banner" :key="index"  @click="bindNews(item.id)">
                            <img :src="item.image" />
                            <div class="slideBox">
                                <img src="@/assets/img/home/play.png" alt="Play" class="playIcon">
                                <div class="slideBoxContent">
                                    <h4>{{item.title}}</h4>
                                    <p>{{item.description}}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="swiper-pagination-art"></div>
            </div>
        </div>
        <div class="list">
            <div class="listItem" v-for="(item,index) in news" :key='index' @click="bindNews(item.id)">
                <div class="listPic">
                    <img :src="item.image" alt="" width="100%">
                </div>
                <div class="listContent">
                    <div class="listTitle">{{item.title}}</div>
                    <div class="listView">{{item.description}}</div>
                    <div class="listMore">
                        <span>Read more</span>
                        <img src="@/assets/img/home/arrow.png" alt="" class="arrowIcon">
                    </div>
                </div>
            </div>
        </div>
    </div>
    <tabbar/>
  </div>
</template>

<script>
import Swiper from 'swiper';
import '../../assets/css/swiper.min.css';
import tabbar from '@/components/tabbar.vue'
import top from '@/components/top.vue'
export default {
    components:{
      tabbar,
      top
    },
    head(){
        return {
            title:''
        }
    },
    created() {
		
	},
    data(){
        return {
          banner:[],
          news:[],
        }
    },
    mounted(){
        var that = this;
        that.getData();
    },
    methods:{
        bindNews:function(id){
            var that = this;
            that.$router.push({name:'News',query:{id:id}})
        },
        getData:function(){
            var that = this;
            that.$http.post(that.$api.getNewsData.url, {}).then(res => {
                if(res.code == 1){
                    that.banner = res.data.lastest;
                    that.news = res.data.history.lists;
                    that.swiper();
                }else{
                    that.$toast(res.msg);
                }
            })
        },
        swiper:function(){
            var that = this;
            that.$nextTick(function(){
                var mySwiper = new Swiper(".swiper-container",{ 
                    slidesPerView: 'auto',
                    spaceBetween: 10,
                    pagination: {
                        el: '.swiper-pagination-art',
                        clickable: true,
                    },
                });
            })
        },
    },
}
</script>

<style lang='scss' scoped>
.homeWrap{
    padding: 0.16rem 0.23rem;
    box-sizing: border-box;
    .title{
        width: 100%;
        height: 0.6rem;
        font-size: 0.46rem;
        font-weight: 500;
        line-height: 0.6rem;
    }
    .bannerBox{
        width: 100%;
        .banner{
            width: 100%;
            margin-top: 0.16rem;
            position: relative;
            .swiper-container {
                width: 100%;
                .swiper-slide {
                    width: 6.20rem;
                    height: 3.31rem;
                    position: relative;
                    >img{
                        width: 100%;
                    }
                    .slideBox{
                        width: 100%;
                        height: 100%;
                        position: absolute;
                        top: 0;
                        left: 0;
                        background: rgba(0,0,0,0.5);
                        padding: 0.24rem 0.3rem;
                        box-sizing: border-box;
                        .playIcon{
                            display: block;
                            width: 0.6rem;
                            height: 0.6rem;
                            position: absolute;
                            bottom: 0.24rem;
                            right: 0.24rem;
                        }
                        .slideBoxContent{
                            width: 100%;
                            height: 100%;
                            color: #ffffff;
                            >h4{
                                font-size: 0.32rem;
                                line-height: 0.38rem;
                                margin-bottom: 0.12rem
                            }
                            >p{
                                font-size: 0.19rem;
                                line-height: 0.27rem;
                            }
                        }
                    }
                }
            }
        }

    }
    .list{
        width: 100%;
        margin-top: 0.25rem;
        .listItem{
            width: 100%;
            height: 2.64rem;
            background: #F0F0F0;
            margin-bottom: 0.25rem;
            overflow: hidden;
            display: flex;
            .listPic{
                width: 3.39rem;
                height: 2.64rem;
                overflow: hidden;
            }
            .listContent{
                width: 3.64rem;
                height: 2.64rem;
                padding: 0.16rem 0.24rem;
                box-sizing: border-box;
                .listTitle{
                    width: 100%;
                    height: 0.4rem;
                    line-height: 0.4rem;
                    font-size: 0.32rem;
                    color: #515050;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
                .listView{
                    width: 100%;
                    height: 1.12rem;
                    margin-top: 0.16rem;
                    font-size: 0.19rem;
                    line-height: 0.28rem;
                    color: #515050;
                    display: -webkit-box;    
                    -webkit-box-orient: vertical;    
                    -webkit-line-clamp: 4;    
                    overflow: hidden;
                }
                .listMore{
                    width: 100%;
                    height: 0.32rem;
                    display: flex;
                    margin-top: 0.36rem;
                    align-items: center;
                    justify-content: space-between;
                    >span{
                        font-size: 0.19rem;
                        color: #000000;
                        font-weight: bold;
                    }
                    .arrowIcon{
                        display: block;
                        width: 0.18rem;
                        height: 0.21rem;
                    }
                }
            }
        }
    }
}
</style>
<style lang="scss">
.swiper-pagination-art{
    position: absolute;
    bottom: 0.17rem;
    left: 0;
    height: 0.1rem;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10;
    .swiper-pagination-bullet{
        display: block;
        width: 0.1rem;
        height: 0.1rem;
        border-radius: 50%;
        background: rgba(255,255,255,0.48);
        margin: 0 0.04rem;
        opacity: 1;
        &.swiper-pagination-bullet-active{
            background: rgba(255,255,255,1);
        }
    }
}
</style>
