<template>
  <div class="container">
    <div class="wrap">
        <div class="title">Questionnaire</div>
        <div class="step">
            <div class="stepNum">{{step + 1}}/5</div>
            <div class="progress">
                <div class="progressValue" :style="{width:progress+'%'}"></div>
            </div>
            <div class="questionBox" v-for="(item,index) in list" :key="index" v-if="step == index">
                <div class="questionTitle">{{item.title}}</div>
                <div class="questionOption">
                    <div class="option"  v-for="(vo,k) in item.options" :key="k" @click="answer(index,k)" :class="{active:vo.checked == 1}">{{vo.text}}</div>
                </div>
            </div>
        </div>
    </div>
    <div class="btns">
        <button class="btn btn01" @click="back">Back</button>
        <button class="btn btn02" @click="next">Continue</button>
    </div>
  </div>
</template>

<script>
export default {
  components:{
      
    },
    head(){
        return {
            title:''
        }
    },
    data(){
        return {
          progress:20,
          step:0,
          list:[],
        }
    },
    mounted(){
        var that = this;
        that.getList();
    },
    methods:{
        next:function(){
            var that = this;
            var status = false;
            for(var i in that.list[that.step].options){
                if(that.list[that.step].options[i].checked == 1){
                    status = true;
                }
            }
            if(!status){
                return false;
            }
            if(that.step < 4){
                that.step ++;
                that.progress = 20 * (that.step + 1);
            }else{
                //var questionData = that.$utils.JsonToString(that.list);
                that.$utils.setCookie('questionData',JSON.stringify(that.list))
                that.$router.push({path:'/sign_up/terms'})
            }
        },
        answer:function(index,k){
            var that = this;
            for(var i in that.list[index].options){
                that.list[index].options[i].checked = 0;
            }
            if(that.step < 4){
                that.list[index].options[k].checked = 1;
            }else{
                that.list[index].options[k].checked = 1;
                that.progress = 20 * (that.step + 1);
            }
            
        },
        back:function(){
            var that = this;
            if(that.step > 0){
                that.step--;
                that.progress = 20 * (that.step + 1);
            }else if(that.step == 0){
                that.$router.go(-1);
            }
        },
        getList:function(){
            var that = this;
            that.$http.get(that.$api.getQuestionList.url, {}).then(res => {
                if(res.code == 1){
                    for(var i in res.data.lists){
                        for(var j in res.data.lists[i].options){
                            res.data.lists[i].options[j].checked = 0
                        }
                    }
                    that.list = res.data.lists;
                }else{
                    that.$toast(res.errmsg);
                }
            })
        }
    },
}
</script>

<style lang='scss' scoped>
.wrap{
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 1.88rem;
    right: 0;
    padding: 0.8rem 0.4rem 0;
    box-sizing: border-box;
    overflow-y: auto;
    .title{
        font-size: 0.46rem;
        line-height: 0.6rem;
    }
    .step{
        width: 100%;
        margin-top: 0.4rem;
        .stepNum{
            width: 100%;
            height: 0.41rem;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            font-size: 0.32rem;
        }
        .progress{
            width: 100%;
            height: 0.24rem;
            border-radius: 0.12rem;
            margin-top: 0.15rem;
            background: rgb(216,216,216);
            overflow: hidden;
            position: relative;
            .progressValue{
                height: 0.24rem;
                background: rgb(219,0,17);
                border-radius: 0.12rem;
                position: absolute;
                top: 0;
                left: 0;
            }
        }
    }
    .questionBox{
        width: 100%;
        margin-top: 0.64rem;
        .questionTitle{
            width: 100%;
            font-size: 0.46rem;
            color: #282014;
            line-height: 0.6rem;
        }
        .questionOption{
            width: 100%;
            margin-top: 0.64rem;
            .option{
                width: 100%;
                padding: 0.3rem;
                box-sizing: border-box;
                border: 0.02rem solid #282014;
                border-radius: 0.02rem;
                font-size: 0.32rem;
                color: #282014;
                line-height: 0.41rem;
                margin-bottom: 0.32rem;
                &.active{
                    background: #4D6474;
                    color: #ffffff;
                    border: 0.02rem solid #4D6474;
                }
            }
        }
    }
}
.btns{
    width: 100%;
    height: 1.88rem;
    padding: 0.4rem;
    box-sizing: border-box;
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0.02rem;
    .btn{
        width: 3.27rem;
        height: 1.18rem;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 0.32rem;
        &.btn01{
            border:1px solid #000000;
            color: #000000;
            box-sizing: border-box;
            background: #ffffff;
        }
        &.btn02{
            background: #DB0011;
            color: #ffffff;
        }
    }
    .errorTips{
        width: 100%;
        
    }
}
</style>
