 
export default {
    setCookie:function(c_name, value, expirehours) {
        var exdate = new Date();
            exdate.setTime(exdate.getTime() + Number(expirehours?expirehours:1) * 3600 * 1000);
            document.cookie = c_name + "=" + value + "; path=/;expires = " + exdate.toGMTString();
    },
    getCookie:function(name) {
        var arr, reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
        if (arr = document.cookie.match(reg)){
               return (arr[2]);
      }else{
            return false
        }
    },
    delCookie:function(name) {
        var exp = new Date();
        exp.setTime(exp.getTime() - 3);
        var cval = this.getCookie(name);
        if (cval){
             document.cookie = name + "=" + cval + "; path=/;expires=" + exp.toGMTString();
        }
    },
    JsonToString:function (o) {    
        var arr = []; 
        var fmt = function(s) { 
            if (typeof s == 'object' && s != null) return JsonToStr(s); 
            return /^(string|number)$/.test(typeof s) ? "'" + s + "'" : s; // return /^(string)$/.test(typeof s) ? '"' + s + '"' : s;
        } 
        for (var i in o) 
            arr.push("'" + i + "':" + fmt(o[i])); 
        return '{' + arr.join(',') + '}'; 
    } 
}