import axios from "axios";
 
// 
axios.interceptors.request.use(
  config => {
    // 1. 
    // 2.  
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);
 
// 
axios.interceptors.response.use(
  response => {
    // 
    // 1. 
    // 2. 
    return response.data;
  },
  error => {
    // 
    if (error && error.response) {
      switch (error.response.status) {
        case 400:
          // 400
          console.log("");
          break;
        case 403:
          //  403 
          console.log("");
          break;
        case 404:
          //  404 
          console.log(",");
          break;
        case 500:
          //  500 
          console.log("");
          break;
        case 502:
          //  502 
          console.log("");
          break;
        case 503:
          //  503 
          console.log("");
          break;
        case 504:
          //  504 
          console.log("");
          break;
        default:
          // 
          return Promise.reject(error);
      }
    } else {
      console.log("");
    }
  }
);