  </div>
</template>
<script>
export default {
    props: {
        backShow:Boolean,
        backType:Boolean,
    },
    data(){
        return {
            
        }
    },
    components: {
        
    },
    async mounted(){
        var that = this;
        
    },
    methods:{
        goBack:function(){
            var that = this;
            if(that.backType == 1){
                that.$router.push({path:'/home'})
            }else{
                that.$router.go(-1);
            }
        },
    }
}
</script>
