<template>
  <div class="container">
    <top :backShow='true'/>
    <div class="homeWrap">
		<div class="title">Frequently asked questions</div>
        <div class="questionList">
            <div class="progress">
                <div class="progressValue" :style="{height:progress+'%',top:progressTop + '%'}"></div>
            </div>
            <vuescroll @handle-scroll="handleScroll" ref="vs">
                <div class="questionListView">
                    <div class="contentItem" v-for="(item,index) in 10" :key="index">
                        <div class="question">Question1: What is your favourite sports?</div>
                        <div class="answer">Answer1: Of course badminton.</div>
                    </div>
                </div>
            </vuescroll>
        </div>
    </div>
    <tabbar/>
  </div>
</template>

<script>
import tabbar from '@/components/tabbar.vue'
import top from '@/components/top.vue'
import vuescroll from 'vuescroll';
export default {
    components:{
      tabbar,
      top,
      vuescroll
    },
    head(){
        return {
            title:''
        }
    },
    data(){
        return {
          progress:0,
          progressTop:0,
        }
    },
    mounted(){
        var that = this;
        that.getScroll();
    },
    methods:{
        getScroll:function(){
            var that = this;
            that.progress = that.$refs["vs"].getCurrentviewDom()[0].offsetHeight / that.$refs["vs"].getCurrentviewDom()[0].scrollHeight * 100;
        },
        handleScroll:function(v,h,n){
            var that = this;
            that.progress = v.barSize * 100;
            that.progressTop = v.process * 100 - v.barSize * 100 *  v.process;
        },
    },
}
</script>

<style lang='scss' scoped>
.homeWrap{
    width: 100%;
    .title{
        width: 100%;
        padding: 0.13rem 0.24rem 0.14rem;
        box-sizing: border-box;
        font-size: 0.46rem;
        font-weight: 500;
        line-height: 0.6rem;
    }
    .questionList{
        position: absolute;
        top: 1.11rem;
        left: 0.25rem;
        right: 0.4rem;
        bottom: 0.4rem;
        font-size: 0.28rem;
        color: #767676;
        line-height: 0.38rem;
        .questionListView{
            width: 100%;
            height: 100%;
            overflow-y: auto;
            padding-left: 0.37rem;
            box-sizing: border-box;
            text-align: left;
            direction:rtl;
            .contentItem{
                width: 100%;
                margin-bottom: 0.53rem;
                .question{
                    width: 100%;
                    line-height: 0.41rem;
                    font-size: 0.32rem;
                    font-weight: bold;
                    margin-bottom: 0.35rem;
                    text-align: left;
                    color: #000000;
                }
                .answer{
                    width: 100%;
                    line-height: 0.41rem;
                    font-size: 0.32rem;
                    text-align: left;
                    color: #000000;
                }
            }
            &::-webkit-scrollbar{
                    width: 0.07rem;
            }
            &::-webkit-scrollbar-track {
                background-color: rgb(216,216,216);
            }
            &::-webkit-scrollbar-thumb {
                background-color:  rgb(219,0,17); 
            }
        }
    }
}
</style>
