<template>
  <div class="container">
    <top :backShow='true'/>
    <div class="homeWrap">
		<div class="title">Share with us</div>
        <div class="banner">
            <div class="bannerTitle">
                <span>Share with us</span>
                <img src="@/assets/img/utils/arrow.png" alt="" class="arrowIcon">
            </div>
            <div class="bannerBox">
                <div class="swiper-container">
                    <div class="swiper-wrapper">
                        <div class="swiper-slide" v-for="(item,index) in banner" :key="index"  @click="bindNews(item.ad_link)">
                            <img :src="item.ad_code" />
                            <div class="slideBox">
                                <div class="slideBoxContent">
                                    <h4>{{item.ad_name}}</h4>
                                    <p>{{item.ad_description}}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="swiper-pagination-art"></div>
            </div>
        </div>
        <div class="list">
            <div class="item" v-for="(item,index) in list" :key="index" @click="bindNews(item.id)">
                <div class="itemTitle">{{item.title}}</div>
                <div class="itemPic">
                    <img :src="item.image" alt="" width="100%">
                </div>
                <div class="itemLabel">
                    <span>{{item.description}}</span>
                    <div class="like">
                        <img src="@/assets/img/utils/star_on.png" alt="" class="likeIcon">
                        <img src="@/assets/img/utils/star_none.png" alt="" class="likeIcon">
                        <em>1000</em>
                    </div>
                </div>
            </div>
        </div>
        <div class="share" @click="share">
            <img src="@/assets/img/play/upload.png" alt="upload" width="100%">
        </div>
    </div>
    <tabbar/>
  </div>
</template>

<script>
import Swiper from 'swiper';
import '../../assets/css/swiper.min.css';
import tabbar from '@/components/tabbar.vue'
import top from '@/components/top.vue'
export default {
    components:{
      tabbar,
      top
    },
    head(){
        return {
            title:''
        }
    },
    created() {
		var that = this;
		that.$nextTick(function(){
			var mySwiper = new Swiper(".swiper-container",{ 
				slidesPerView: 'auto',
                spaceBetween: 10,
                pagination: {
                    el: '.swiper-pagination-art',
                    clickable: true,
                },
			});
		})
	},
    data(){
        return {
          banner:[],
          list:[],
        }
    },
    mounted(){
        var that = this;
        that.getDetail();
    },
    methods:{
        getDetail:function(){
            var that = this;
            that.$http.post(that.$api.getInteraction.url, {}).then(res => {
                if(res.code == 1){
                    that.banner = res.data.adList;
                    that.list = res.data.interactionZone.lists;
                }else{
                    that.$toast(res.msg);
                }
            })
        },
        bindNews:function(id){
            var that = this;
            that.$router.push({name:'News',query:{id:id}})
        },
        share:function(){
            var that = this;
            that.$router.push({path:'/play/share_view'})
        }
    },
}
</script>

<style lang='scss' scoped>
.homeWrap{
    .title{
        width: 100%;
        padding: 0.24rem 0.24rem 0;
        box-sizing: border-box;
        font-size: 0.46rem;
        font-weight: 500;
        line-height: 0.6rem;
    }
    .banner{
        width: 100%;
        margin-top: 0.08rem;
        .bannerTitle{
            width: 100%;
            height: 0.48rem;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 0 0.24rem;
            box-sizing: border-box;
            >span{
                font-size: 0.32rem;
            }
            .arrowIcon{
                display: block;
                width: 0.18rem;
                height: 0.21rem;
            }
        }
        .bannerBox{
            width: 100%;
            padding-left: 0.24rem;
            box-sizing: border-box;
            margin-top: 0.16rem;
            position: relative;
            .swiper-container {
                width: 100%;
                .swiper-slide {
                    width: 6.69rem;
                    height: 3.30rem;
                    position: relative;
                    >img{
                        width: 100%;
                    }
                    .slideBox{
                        width: 100%;
                        height: 100%;
                        position: absolute;
                        top: 0;
                        left: 0;
                        // background: rgba(0,0,0,0.5);
                        padding: 0.24rem 0.25rem;
                        box-sizing: border-box;
                        .slideBoxContent{
                            width: 100%;
                            height: 100%;
                            color: #ffffff;
                            >h4{
                                font-size: 0.36rem;
                                line-height: 0.46rem;
                                margin-bottom: 0.12rem;
                                font-weight:500;
                            }
                            >p{
                                font-size: 0.18rem;
                                line-height: 0.23rem;
                            }
                        }
                    }
                }
            }
        }
    }
    .list{
        width: 100%;
        margin-top: 0.32rem;
        padding: 0 0.24rem;
        box-sizing: border-box;
        .item{
            width: 100%;
            margin-bottom: 0.32rem;
            .itemTitle{
                width: 100%;
                line-height: 0.41rem;
                font-size: 0.32rem;
                font-weight: 500;
            }
            .itemPic{
                width: 100%;
                height: 2.5rem;
                margin-top: 0.12rem;
                overflow: hidden;
                >img{
                    width: 100%;
                }
            }
            .itemLabel{
                width: 100%;
                margin-top: 0.12rem;
                height: 0.36rem;
                display: flex;
                align-items: flex-start;
                justify-content: space-between;
                >span{
                    display: block;
                    width: 5.2rem;
                    font-size: 0.28rem;
                    color: #999999;
                    line-height: 0.36rem;
                }
                .like{
                    height: 0.37rem;
                    display: flex;
                    align-items: center;
                    .likeIcon{
                        display: block;
                        width: 0.44rem;
                        height: 0.37rem;
                    }
                    >em{
                        font-size: 0.19rem;
                        color: #9B9B9B;
                        margin-left: 0.07rem;
                    }
                }
            }
        }
    }
    .share{
        width: 0.96rem;
        height: 0.96rem;
        position: fixed;
        bottom: 1.15rem;
        right: 0.16rem;
        z-index: 5;
    }
}
</style>
