<template>
  <div class="container">
    <div class="wrap">
        <logo/> 
        <div class="view">
            <div class="title">{{ctype==1?'':title}}</div>
            <div class="content">{{ctype==1?content_01:content_02}}</div>
        </div>
        <div class="btns">
          <div class="btn btnRed" @click="bind" v-if="ctype == 1">Continue</div>
          <div class="btn btnRed" @click="logon" v-if="ctype == 2">Log on</div>
          <div class="btn btnBlack" v-if="ctype == 2" @click="reg">Register</div>
        </div>
    </div>
    <div class="popMask" v-if="popStatus">
      <div class="popBox">
        <div class="popClose"><img src="@/assets/img/utils/close.png" alt="close" @click="closePop"></div>
        <div class="title">Cookies on this website</div>
        
        <div class="content">
          <div class="progress">
                <div class="progressValue" :style="{height:progress+'%',top:progressTop + '%'}"></div>
            </div>
          <vuescroll @handle-scroll="handleScroll" ref="vs">
            <div class="contentBox">
              <span v-for="(item,index) in 20" :key="index">This website uses cookies to improve your experiences. By clicking OK or continuing to browse the site you are agreeing to our use of cookies.</span>
            </div>
          </vuescroll>
        </div>
        
        <div class="btn" @click="closePop">OK</div>
      </div>
    </div>
  </div>
</template>

<script>
import logo from '@/components/logo.vue'
import vuescroll from 'vuescroll';
export default {
    components:{
      logo,
      vuescroll
    },
    head(){
        return {
            title:''
        }
    },
    data(){
        return {
          popStatus:true,
          ctype:1,
          title:'Welcome',
          content_01:'You will now access this site in mobile view.',
          content_02:'Register or log on to the Badminton Hub now.',
          progress:0,
          progressTop:0,
        }
    },
    mounted(){
        var that = this;
        that.getScroll();
    },
    methods:{
      closePop:function(){
        var that = this;
        that.popStatus = false;
      },
      getScroll:function(){
        var that = this;
        console.log(that.$refs["vs"].getCurrentviewDom()[0].offsetHeight)
        console.log(that.$refs["vs"].getCurrentviewDom()[0].scrollHeight)
        that.progress = that.$refs["vs"].getCurrentviewDom()[0].offsetHeight / that.$refs["vs"].getCurrentviewDom()[0].scrollHeight * 100;
      },
      handleScroll:function(v,h,n){
        var that = this;
        that.progress = v.barSize * 100;
        that.progressTop = v.process * 100 - v.barSize * 100 *  v.process;
        
        console.log(v)
      },
      reg:function(){
        var that = this;
        that.$router.push({path:'/sign_up'})
      },
      logon:function(){
        var that = this;
        that.$router.push({path:'/logon/on'})
      },
      bind:function(){
        var that = this;
        that.ctype = 2;
      }
    },
}
</script>

<style lang='scss' scoped>
.wrap{
    width: 100%;
    padding-top: 2.88rem;
    box-sizing: border-box;
    .view{
        width: 100%;
        padding: 0 0.4rem;
        box-sizing: border-box;
        .title{
          width: 100%;
          height: 0.8rem;
          font-size: 0.66rem;
          line-height: 0.8rem;
          color: #000000;
        }
        .content{
          width: 100%;
          font-size: 0.46rem;
          color: #000000;
          line-height: 0.8rem;
          word-break:break-all;
        }
    }
    .btns{
      width: 100%;
      padding: 0.4rem;
      box-sizing: border-box;
      position: fixed;
      bottom: 0;
      left: 0;
      .btn{
        width: 100%;
        height: 1.18rem;
        border-radius: 0.02rem;
        font-size: 0.32rem;
        display: flex;
        align-items: center;
        justify-content: center;
        &.btnRed{
          background: #DB0011;
          color: #ffffff;
        }
        &.btnBlack{
          border: 0.02rem solid #000000;
          color: #000000;
          margin-top: 0.32rem;
        }
      }
    }
}
.popMask{
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0,0,0,0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  .popBox{
    width: 6.86rem;
    height: 8.99rem;
    background: #FFFFFF;
    box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.1);
    position: relative;
    padding: 0.32rem;
    box-sizing: border-box;
    .popClose{
      width: 0.2rem;
      height: 0.2rem;
      position: absolute;
      top: 0.24rem;
      right: 0.24rem;
      >img{
        width: 100%;
      }
    }
    .title{
      width: 100%;
      line-height: 0.65rem;
      font-size: 0.46rem;
      color: #333333;
      font-weight: 500;
    }
    .content{
      width: 100%;
      height: 6.3rem;
      margin-top: 0.2rem;
      text-align: left;
      font-size: 0.28rem;
      line-height: 0.38rem;
      box-sizing: border-box;
      position: relative;
      .contentBox{
        width: 100%;
        padding-left: 0.4rem;
        box-sizing: border-box;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
        text-align: left;
      }
      &::-webkit-scrollbar{
              width: 0.07rem;
      }
      &::-webkit-scrollbar-track {
          background-color: rgb(216,216,216);
      }
      &::-webkit-scrollbar-thumb {
          background-color:  rgb(219,0,17); 
      }
    }
    .btn{
      width: 100%;
      height: 0.9rem;
      border: 1px solid #000000;
      margin-top: 0.3rem;
      font-size: 0.32rem;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}
</style>
